import * as EmailValidator from 'email-validator';
import throttle from 'lodash/throttle';

let arrayInputs = document.getElementsByClassName(
  'contact__form-input'
);
let nameInput = arrayInputs[0];
let email = document.getElementById('mail');
let messageInput = arrayInputs[2];
let button = document.querySelector(
  '.contact__form-button'
);
let form = document.querySelector(
  '.contact__form-fields'
);
let wrongValue = document.querySelector(
  '.wrong__input'
);
if (
  nameInput.value !== '' &&
  messageInput.value !== '' &&
  email !== ''
) {
  button.removeAttribute('disabled');
}
form.addEventListener('submit', handleSubmit);

let message = document.querySelector(
  '.error-invisible'
);
email.addEventListener('change', onTextareaInput);

function handleSubmit(event) {
  event.preventDefault();
  const {
    elements: { message, email, name },
  } = event.currentTarget;
}
function onTextareaInput(evt) {
  let k = evt.target.value;
  console.log(
    EmailValidator.validate(k) === true
  );
  let trigger = false;

  if (EmailValidator.validate(k) === true) {
    email.classList.remove('contact__form-input');
    email.classList.add('correct__input');
    console.log(wrongValue);
  }
  if (
    EmailValidator.validate(k) === false &&
    wrongValue === null
  ) {
    trigger = true;
    email.classList.remove('contact__form-input');
    email.classList.add('wrong__input');
    console.log(wrongValue);
  }
  if (
    EmailValidator.validate(k) === true &&
    wrongValue !== null
  ) {
    trigger = true;
    email.classList.remove('wrong__input');
    email.classList.add('correct__input');
    console.log(wrongValue);
  }
  if (trigger === true) {
    message.style.display = 'block';
  }
  if (evt.currentTarget.value === '') {
    email.classList.remove('correct__input');
    email.classList.remove('wrong__input');
    email.classList.add('contact__form-input');
    message.style.display = 'none';
  }
}
